.logo {
  height: 40px;
}

.wbdv-navbar {
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
  padding: .6rem .6rem;
  background: black;
}

.wbdv-navbar a {
  float: left;
  text-align: center;
  padding: 6px 6px;
  text-decoration: none;
  font-size: 17px;
  border-radius: .25rem;
}


/* Add a red background color to navbar links on hover */
.wbdv-navbar a:hover, .wbdv-dropdown:hover .wbdv-hamburger {
  background-color: green;
}

.wbdv-navbar a.active {
  background-color: #4CAF50;
  color: white;
}


.wbdv-label {
  float: left;
  line-height:40px;
  font-size: 24px;
  font-family: Impact, Charcoal, sans-serif;
  font-weight: 600;
  color: white;
  text-align: center;
  text-decoration: none;
}
